body {
  background-color: #9266ff;
}

.App {
  background-color: #3f3160;
  min-height:500px;
  width:400px;
  margin:40px auto;
}

#to-do-form input {
  background-color: rgb(27,112,132);
  border:0;
  padding:0 20px;
  margin:20px;
  width:200px;
  height:50px;
  border-radius: 10px;
  font-size:18px;
  color:#fff;
}
#to-do-form input::placeholder {
  color: rgba(255,255,255,0.5);
}

#to-do-form input:focus, #to-do-form button {
  outline:none;
}

#to-do-form button {
  cursor: pointer;
  height:50px;
  width:80px;
  border-radius: 5px;
  background-color: rgb(202,156,24);
  border:0;
  font-weight: bolder;
  color:#fff;
}